.menu{
    display: flex;
    background: $gray-300;
    position: relative;
    z-index: 4;
}
.menu-level-1{
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    font-size: 14px;
}
.menu-level-1 > li{
    height: 50px;
}
.menu-level-1 > li.ext{
    &:hover, &:active, &:focus{
        text-decoration: none;
        background: $white;
    }
}
.menu-level-1 > li > a{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $gray-800;
    padding: 0 20px 0 20px;

    &:hover, &:active, &:focus{
        text-decoration: none;
        background: $white;
    }
}
.menu-level-1 > li > a > b{
    font-weight: 500;
}
.menu-level-1 > li:nth-child(4){
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
}
@media screen and (max-width: 991px){
    .menu-level-1 > li:nth-child(4){
        justify-content: flex-start;
        flex-grow: 0;
    }  
}
.menu-level-2{
    list-style: none;
    padding: 0;
    display: none;
    position: absolute;
    z-index: 1;
    background: $white;
    left: 0;
    width: 100%;
    padding: 10px 0;
    flex-wrap: wrap;
}

@media screen and (min-width: 992px){
    .menu-level-1 > li:hover .menu-level-2{
        display: flex;
    }
}
.menu-level-2 > li{
    padding: 10px 20px;
    display: flex;
    align-items: flex-start;
    width: 33.333%;
}
.menu-level-2 > li > a{
    flex-shrink: 0;
}
.menu-level-2 > li > a > img{
    height: 80px;
}
.menu-level-2 > li > div{
    height: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding-left: 10px;
}
.menu-level-2 > li > div > a{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    color: $gray-800;
    margin-top: 30px;
    font-weight: 500;

    &:hover, &:active, &:focus{
        text-decoration: none;
        background: transparent;
    }
}
.menu-level-2 > li > div > a > span{

}

.menu-level-3{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
}
.menu-level-3 > li{
    font-size: 13px;
}
.menu-level-3 > li > a{
    color: $gray-700;
}



.menu-level-1>li.ext >a{
    position:relative;
    padding: 0 40px 0 20px;
}
.menu-level-1>li.ext >a::after{
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background: #ccc;
}
.menu-level-1>li.ext >a::after {
    content: none;
}
.menu-level-1::before{
    content: "";
    width: 1px;
    height: 26px;
    background: $gray-400;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    z-index: -1;
}

.menu-level-1>li.ext>a::before{
    content: "";
    width: 1px;
    height: 26px;
    background: $gray-400;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
}
.menu-level-1>li.ext>a::after{
    content: "";
    width: 20px;
    height: 20px;
    background-color: transparent;
    background-image: url($i-arrow-down);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 14px 14px;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
}

.menu-helper{
    position: fixed;
    top: 0px;
    display: none;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba($black,0.8);
    z-index: 3;
}

@media screen and (max-width: 991px){
    body.--noScroll .menu-helper{
        display: flex;
    }   
}


.menu-helper *{
    display: none;
}


@media screen and (max-width: 991px){
    header::before {
        content: none;
    }
    .menu{
        position: fixed;
        left: 0;
        top: 56px;
        z-index: 4;
        overflow-y: auto;
        height: 100%;
        width: 260px;
        transform: translate(-100%, 0);
        transition: 0.3s ease transform;
        background: $white;
    }
    .menu-level-1{
        flex-direction: column;
    }
    .menu-level-1 > li{
        height: auto;
        position: relative;
    }
    .menu-level-1 > li > a{
        justify-content: flex-start;
        height: 50px;
        padding: 0 20px;
        width: 100%;
    }
    .menu-level-1 > li.ext > a{
        padding: 0 40px 0 60px;
        background-size: 30px;
        background-position: left 20px center;
        background-repeat: no-repeat;
    }
    .menu-level-1 > li.ext > a[href="/detsky-nabytek/"], .menu-level-1 > li.ext > a[href="/baby-and-children-s-furniture/"]{
        background-image: url("../img/detsky-nabytek.png");
    }
    .menu-level-1 > li.ext > a[href="/nabytek/"], .menu-level-1 > li.ext > a[href="/furniture/"]{
        background-image: url("../img/nabytek.png");
    }
    .menu-level-1 > li.ext > a[href="/textil/"], .menu-level-1 > li.ext > a[href="/textiles/"]{
        background-image: url("../img/textil.png");
    }
    .menu-level-1 > li > a > b{
        font-weight: 600;
    }
    .menu-level-2{
        position: relative;
        flex-direction: column;
        background: $white;
        display: flex;
        flex-direction: column;
        padding: 0;
        display: none;

        &.--active{
            display: flex;
        }
    }
    .menu-level-2 > li {
        justify-content: flex-start;
        width: 100%;
        padding: 0;
        align-items: center;
    }
    .menu-level-2 > li > div{
        padding: 0;
        width: 100%;
    }
    .menu-level-2 > li > div > a{
        display: flex;
        justify-content: flex-start;
        width: 100%;
        padding: 5px 15px 5px 30px;
        color: $gray-700;
        margin: 0;
    }
    .menu-level-2 .menu-image {
        height: 36px;
        display: none;
    }
    .menu-level-2 > li > a > img {
        height: 100%;
    }
    .menu-level-3{
        display: none;
    }
}

.language-toggle{
    overflow: hidden;
    background: transparent;
    height: 20px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
}
.language-toggle > div{
    box-shadow: 0px 4px 15px rgba($black,0.08);
}
.language-toggle.--active{
    background: $white;
    overflow: visible;
}
.language-toggle.--active .language-toggle-item{
    background-color: $white;
    height: 30px;
    z-index: 5;
    position: relative;
}
.language-toggle-item{
    height: 20px;
    font-size: 0px;
    padding: 0 10px;
    height: 20px;
    width: 40px;
    flex-shrink: 0;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    &:hover, &:active, &:focus{
        text-decoration: none;
    }
}
.language-toggle-item.cz{
    background-image: url($i-flag-cz);
}
.language-toggle-item.sk{
    background-image: url($i-flag-sk);
}
.language-toggle-item.com{
    background-image: url($i-flag-com);
}
.language-toggle-item.active{
    
}

.navLinks{
    display: flex;
    padding-bottom: 5px;
    justify-content: flex-end;
    width: 100%;
}
.navLinks__link{
    color: $gray-700;
    font-size: 14px;
    padding: 0 10px;
    border-left: 1px solid $gray-400;
    border-right: 1px solid $gray-400;

    &.--user{
        padding-left: 24px;
        background-image: url($i-user);
        height: 18px;
        width: 18px;
        background-repeat: no-repeat;
        background-size: contain;
        width: auto;
    }
}
.navLinks__link:first-child{
    border: 0;
}
@media screen and (max-width: 991px){
    .navLinks{
        display: none;
    }
}
.navLinks__span{
    font-size: 14px;
    padding: 0 0px 0 10px;
}

.free-delivery{
    margin-top: 15px;
    margin-bottom: 13px;
    font-weight: 600;
    font-size: 18px;
}
.free-delivery span{
    color: $green-700;
}

.headerFreeDelivery{
    margin-top: 5px;
    text-align: right;
    line-height: 1.2;
}
.headerFreeDelivery span{
    color: $red-700;
    font-weight: 600;
}
.headerFreeDelivery.free{
    color: $green-800;
    font-weight: 600;
}
@media screen and (max-width: 991px){
    .headerFreeDelivery{
        display: none;
    }
}