html{

}
body{
    font-size: 16px;
    color: $gray-800;
    background: $white;
    font-family: $font-family-system;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    margin: 0 auto;
    transition: 0.3s ease transform;
}
@media screen and (max-width: 991px){
    body.--noScroll{
        overflow: hidden;
    }
    body.--active{
        transform: translateX(200px);
    }
}
@media screen and (min-width: 992px){
    body.submenu-active{
        overflow-y: scroll;
    }
    body:not(.ordering-process):not(.type-detail) .overall-wrapper{
        padding-top: 160px;
    }
    body:not(.ordering-process):not(.type-detail) #header{
        position: fixed;
        width: 100%;
        z-index: 2;
        top: 0;
    }
}
@media (min-width: 992px){
    body.ordering-process::after{
        content: "";
        height: 300px;
        width: 100%;
        display: flex;
        background-image: url("../img/bg.png");
        background-position: center top;
    }
}

/* Change Autocomplete styles in Chrome
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
}*/

::selection {
    background: $green-200;
}

a{
    color: $green-700;
    text-decoration: none;
    transition: 0.3s ease all;

    &:hover, &:active, &:focus{
        color: $gray-800;
    }
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, 
.col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, 
.col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, 
.col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, 
.col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, 
.col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, 
.col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, 
.col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto{
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
}
.container {
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
}
.row{
    margin-right: -10px;
    margin-left: -10px;
}
@media (min-width: 1220px){
    .container {
        max-width: 1220px;
    }
}
@media (max-width: 1219px){
    .container {
        max-width: 100%;
    }
}
@media (max-width: 991px){
    .container {
        max-width: 100%;
        padding-right: 10px;
        padding-left: 10px;
    }
}


header + .content-wrapper .content-wrapper-in{
    padding: 0;
}

.col-xs-12{
    width: 100%;
    padding: 0 10px;
}
.no-display{
    display: none;
}

.content{
    width: 100%;
}
.type-post .content-wrapper-in, .type-posts-listing .content-wrapper-in{
    flex-wrap: nowrap;
}
.content > h4{
    display: none;
}


.middle-banners-wrapper .banner-wrapper{  
    height: auto;
}
.middle-banners-wrapper .banner-wrapper > span{  
    width: 100%;
}

/*
.js-hidden{
    display: none;
}
*/

.type-category .sidebar-inner div[class*="box-section"]{
    display: none;
}
.content-inner header h1{
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 40px;
    padding-bottom: 15px;
    color: $gray-800;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        height: 6px;
        width: 40px;
        background: $green-200;
    }
}


.type-detail .overall-wrapper::before, .type-category .overall-wrapper::before{
    content: "";
    background-image: url("../img/bg.png");
    background-position: center top;
    background-repeat: repeat no-repeat;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 300px;
    z-index: -1;
}
.type-detail .content-wrapper, .type-category .content-wrapper{
    background: $white;
}


.force-display{
    display: block !important;
}
.force-display.empty{
    display: none !important;
}
.force-display ~ .default-variant{
    display: none !important;
}


