.login-close{
    position: absolute;
    width: 40px;
    height: 40px;
    right: 0;
    top: 0;  
    background-color: rgba($black, 0.1);
    background-image: url("../icons/close.svg");
    background-repeat: no-repeat;
    background-size: 18px;
    background-position: center center;
    cursor: pointer;
}

.extras-product-heading{
    display: flex;
    width: 100%;
    padding-bottom: 5px;

    span{
        color: $gray-700;
        font-size: 13px;
    }
    span:first-child{
        width: calc(25% + 80px);
    }
    span{
        width:25%;
    }
    span:last-child{
        width: auto;
        flex-grow: 1;
    }
}

.extras-product{
    display: flex;
    border-top: 1px solid $gray-400;
    border-bottom: 1px solid $gray-400;
    padding: 10px 0;
    margin-bottom: 20px;
    width: 100%;
    font-size: 14px;
}
.extras-product-img{
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.extras-product-name{
    padding: 0 20px;
    width: 25%;
    align-items: center;
    display: flex;
}
.extras-product-stock{
    width: 25%;
    color: $green-700;
    align-items: center;
    display: flex;
}
.extras-product-amount{
    width: 25%;
    align-items: center;
    display: flex;
}
.extras-product-priceSingle{
    flex-grow: 1;
    align-items: center;
    display: flex;
    color: $red-700;
    font-weight: 700;
    display: none;

    .price-final-currency{
        font-size: 14px;
    }
}
.extras-product-priceTotal{
    flex-grow: 1;
    align-items: center;
    display: flex;
}

.login-form-register-title{
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
    padding-bottom: 15px;
    color: $gray-800;
    margin-top: 40px;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        height: 6px;
        width: 40px;
        background: $green-200;
    }
}
.login-form-points{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login-form-points-wrap{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.login-form-point{
    width: 50%;
    font-size: 14px;
    text-align: left;
    color: $gray-900;
    padding: 3px 0;
    position: relative;
    display: flex;
    align-items: center;

    &::before{
        content: "";
        height: 8px;
        width: 8px;
        flex-shrink: 0;
        border-radius: 50%;
        background: $green-200;
        display: flex;
        margin-right: 15px;
    }
}

@media (max-width: 500px){
    .login-form-point{
        width: 100%;
    }
    .in-login .content-inner h1, .login-form-register-title{
        font-size: 20px;
        margin-top: 0px;
    }
}






